<template>
  <div id="address" class="bg-fff" @click="linkTo('ConfirmAddress')">
    <div class="address-info">
      <template v-if="confirmAddress">
        <div class="name-phone clearfix">
          <span class="fl-left">收货人：{{confirmAddress.name}}</span>
          <span class="fl-right">{{getMobile}}</span>
        </div>
        <div class="address-detail">
          {{confirmAddress.regionInfo[0].region.name}}{{confirmAddress.regionInfo[1].region.name}}{{confirmAddress.regionInfo[2].region.name}}{{confirmAddress.street}}
        </div>
        <img src="../../assets/arrow_gray.png" class="arrow_gray">
    	</template>
    	<template v-else>未选择</template>
    </div>
    <img src="../../assets/cart/address_bor.png" class="address-bor">
  </div>
</template>

<script type="text/javascript">
	export default{
		props:{
			confirmAddress:{
				type:Object,
				default:{}
			}
		},
		computed:{
			getMobile(){
    		if(this.confirmAddress){
    			let phone = this.confirmAddress.mobile.toString()
    			let mobile = phone.substring(0,3) + '****' + phone.substring(7,11)
    			return mobile
    		}
    	},
		}
	}
</script>
<style lang="scss">
  @import "../../sass/baseSass.scss";

  .arrow_gray {
    @include rt-arrow;
  }

  #address {
    padding:  0.34rem 0.41rem 0.39rem;
    position: relative;
    font-size: 0.37rem;
    color: #333;

    .address-info{
      width: 100%;
      padding: 0 0.45rem 0 0.7rem;
      background: url("../../assets/cart/ico_location.png") no-repeat center left;
      background-size: 0.43rem auto;
    }
    .address-detail{
      margin-top: 0.35rem;
    }
  }

  img.address-bor{
    width: 100%;
    height: 0.05rem;
    position: absolute;
    bottom: 0;
    left: 0;
  }
</style>




