<template>
  <div id="send">
  	<commonHeader :title="'实物发货'"></commonHeader>
    <Gift :gifts="gifts"/>
    <Address :confirmAddress="confirmAddress"/>
    <Cell :title="'发货数量'" :type="'count'" :num="value" :max="maxNum" @change="valueChange"/>
    <Cell :title="'留言'" :type="'input'" placeholder="选填" @input="getNote"/>

    <div class="submit__btn" :class="{'active': true}" @click="confirm">确认发货</div>
  </div>
</template>

<script>
  import Gift from '@/components/Goods/Goods';
  import Address from '@/components/Address/Address';
  import Cell from '@/components/Cell/Cell';
  
  export default {
    data() {
      return {
        value: 1,
        gifts:[],
        confirmAddress:{},
        note:'',
        skuId:'',
        spuId:'',
        promId:'',
        customId:'',
        spuType:'',
        orgId:'',
      }
    },
    created(){
    	this.getGiftDetail()
    	this.initAddress()
    },
    computed:{
    	maxNum(){
    		let num = 0
    		if(this.gifts.length > 0){
    			num = this.gifts[0].num
    		}
    		return num
    	}
    },
    methods: {
      getNote(val) {
        this.note = val;
        console.log(val)
      },
      valueChange(val) {
        this.value += val;
      },
      getGiftDetail(){
      	let a = 1
      	let packageId = this.$route.query.packageId
      	this.$Api.getGiftDetails(a,packageId).then((res)=>{
      		console.log(res)
      		if(res.q.s == 0){
			      this.gifts = res.q.gifts
			      this.packageId = res.q.packageId
      		}
      	})
      },
      initAddress(){
      	//判断是否已经选择
      	if(localStorage.getItem('confirmAddress')){
      		let confirmAddress = JSON.parse(localStorage.getItem('confirmAddress'))
      		this.confirmAddress = confirmAddress
      		console.log(confirmAddress)
      	}else{
					//获取默认地址
					this.$Api.getShippingAddressList().then((res)=>{
						console.log(res)
						if(res.q.s == 0){
							if(res.q.total > 0){
								let addresses = res.q.addresses
								addresses.forEach((item)=>{
									if(item.selected == 1){
										console.log(item)
										this.confirmAddress = item
									}
								})
							}
						}
					})
      	}
      },
      confirm(){
      	let shops = []
      	let gift = this.gifts[0]
      	
      	let shopObj = {
      		orgId:gift.orgId,
      		note:this.note,
      		coupons:'',
      		goodsList:[{
						skuId:gift.skuId,
						spuId:gift.spuId,
						num:gift.num,
						promId:gift.promId,
						customId:gift.customId?gift.customId:'0',
						storeId:gift.storeId,
						packingId:this.packageId
					}]
      	}
      	shops.push(shopObj)
      	let params = {
      		a:3,
      		t:gift.spuType,
      		sendType:2,
//    		addresId:this.confirmAddress.id?this.confirmAddress.id:'0',
      		addresId:this.confirmAddress.id,
      		shops:shops,
      	}
      	this.$Api.getOrderSubmit(params).then((res)=>{
      		if(res.q.s == 0){
      			this.$toast(res.q.d)
      			setTimeout(()=>{
      				this.$router.replace({name:'Gift'})
      			},1500)
      		}else{
      			this.$toast(res.q.d)
      		}
      	})
      }
    },
    components: {
      Gift,
      Cell,
      Address
    }
  }
</script>


<style lang="scss">
  

  #send {
    height: 100%;
    background: #f0f0f0;

    #giftDetail__good {
      margin-top: 0;
      border-bottom: 1px solid #eee;
    }
  }
</style>




